import { Component, OnInit } from '@angular/core';
import { DataService } from './../../../services/data.service';
declare var jquery: any;
declare var $: any;
import { MatSnackBar } from '@angular/material';

@Component({
  selector: 'units-management',
  templateUrl: './units-management.component.html',
  styleUrls: ['./units-management.component.scss']
})
export class UnitsManagementComponent implements OnInit {
  frontVisible: boolean = true;
  user: any;
  data: any = {};
  callGrid: boolean;
  fields: any = [];
  newRecord: any = {};
  years: any = [];
  staff: any = [];
  phase: any = [];
  constructor(private dataService: DataService, public snackBar: MatSnackBar) { }

  ngOnInit() {
    this.dataService.getDetails();
    this.user = this.dataService.user;
    this.setValues();
  }
  setValues(){
    this.dataService.getRequest('staffs').subscribe(results => {
      for (var i = 0; i < results.length; i++) {
        this.staff.push({ id: results[0].id, value: results[0].firstName + results[0].lastName });
      }
      this.dataService.getRequest('phase').subscribe(results => {
        for (let i = 0; i < results.length; i++) {
          this.phase.push({ id: results[0].id, value: results[0].phase });
        }
        this.fields = [
          {
            field: 'employees',
            label: 'Employees',
            element: 'dropdown',
            hide: false,
            editable: true,
            update: true,
            value: true,
            values: this.staff,
            multiple: true
          },
          {
            field: 'id',
            label: 'Unit Id',
            element: 'input',
            remove: true,
            updateField: true,
            hide: true,
            editable: false,
            update: true
          },
          {
            field: 'noOfCattles',
            label: 'Number Of Cattles',
            element: 'number',
            hide: false,
            editable: true,
            update: true
          },
          {
            field: 'phase',
            label: 'Phase',
            element: 'dropdown',
            hide: false,
            editable: true,
            value: true,
            values: this.phase,
            update: true
          },
          {
            field: 'unit',
            label: 'Unit',
            element: 'input',
            hide: false,
            editable: true,
            update: true
          },
          {
            field: 'actions',
            label: 'Actions',
            element: '',
            hide: false,
            editable: true
          }
        ];
        this.getData();
      });
    });
  }
  ngAfterContentChecked() {
    this.getHeightWidth();
  }
  getHeightWidth() {
    let height = ($('.nav-list')[0].offsetTop - $('.students-view .card')[0].offsetTop),
      width = $('.sidenav-body')[0].offsetWidth;
    this.data.width = width - 45;
    this.data.height = height - 82;
    this.data.showFilter = true;
    this.data.showFooter = false;
    this.data.updatable = true;
    this.data.deletable = true;
  }
  getData() {
    let url = 'unit';
    this.data.url = url;
    this.data.deleteUrl = 'unit/';
    this.data.deleteMethod = true;
    this.data.editUrl = 'unit';
    this.data.data = [];
    this.dataService.getRequest(url).subscribe(results => {
      if (!results.length) {
        this.data.data = [];
      } else
        this.data.data = results;
      this.newRecord = {};
      this.callGrid = true;
    });
  }


  addNewStudent(student) {
    let temp = {
      "noOfCattles": student.noOfCattles,
      "phase": student.phase,
      "unit": student.unit,
      "employees": student.employees
    }
    let url = "unit";
    this.data.data = [];
    this.dataService.getData(url, temp).subscribe(results => {
      if (!results) {
        this.snackBar.open('Error Adding Unit Details', 'Ok', {
          duration: 5000,
        });
        return;
      }
      this.dataService.getRequest(url).subscribe(results => {
        if (!results.length) {
          this.data.data = [];
          return;
        }
        this.flip();
        this.data.data = results;
        this.snackBar.open('Unit Deatils Added Succesfully', 'Ok', {
          duration: 5000,
        });
        this.newRecord = {};
      });
    });
  }

  onDelete(flag) {
    if (!flag.flag) {
      this.snackBar.open('Unit Details ' + (flag.text || flag) + ' Succesfully', 'Ok', {
        duration: 5000,
      });
    }
    this.callGrid = false;
    this.getData();
  }
  flip() {
    this.setValues();
    $('.card').toggleClass('flipped');
    this.frontVisible = !this.frontVisible;
  }
}



