import { Component, OnInit } from '@angular/core';
import { TodoDataService } from '../../todo-data.service';
import { Todo } from '../../todo';
import { DataService } from './../../services/data.service';

@Component({
  selector: 'todo-list',
  templateUrl: './todo-list.component.html',
  styleUrls: ['./todo-list.component.scss']
})
export class TodoListComponent implements OnInit {

  newTodo: Todo = new Todo();
  todos: any;
  constructor(private todoDataService: TodoDataService, private dataService: DataService) {
  }

  addTodo() {
    var todo = this.newTodo;
    if (!this.todos)
      this.todos = [];
    this.todos.push(todo);
    let temp = {
      "message": todo.message,
      "userName": this.dataService.user.userName
    }
    console.log(temp);
    this.dataService.getData('todolists', temp).subscribe(results => {
      if (!results || results.code === -1) {
        return;
      }
      this.getTodos();
    });
    this.newTodo = new Todo();
    this.todoDataService.addTodo(this.newTodo);
  }

  toggleTodoComplete(todo) {
    // todo.isActive = todo.isActive === "1" ? "0" : "1";
    // this.dataService.putData('todolists/'+todo.id, todo).subscribe(results => {
    //   if (!results || results.code === -1) {
    //     return;
    //   }
    // });
    //this.todoDataService.toggleTodoComplete(todo);
  }

  removeTodo(todo) {
    //this.todoDataService.deleteTodoById(todo.id);
    this.dataService.deleteData('todolists/' + todo.id).subscribe(results => {
      this.getTodos();
      if (!results || results.code === -1) {
        return;
      }
      this.getTodos();
    });

  }

  // get todos() {
  //   return  this.todoDataService.getAllTodos();
  // }
  getTodos() {
    this.todos = [];
    this.dataService.getRequest('todolists/' + this.dataService.user.userName).subscribe(results => {
      if (!results || results.code === -1) {
        this.todos = [];
        return;
      }
      this.todos = results;
    });
  }
  ngOnInit() {
    this.getTodos();
    //  return  this.todoDataService.getAllTodos();
  }

}



