import { Component, OnInit } from '@angular/core';
import { DataService } from './../../../services/data.service';
declare var jquery: any;
declare var $: any;
import { MatSnackBar } from '@angular/material';

@Component({
  selector: 'customers-management',
  templateUrl: './customers-management.component.html',
  styleUrls: ['./customers-management.component.scss']
})
export class CustomersManagementComponent implements OnInit {
  frontVisible: boolean = true;
  user: any;
  data: any = {};
  callGrid: boolean;
  fields: any = [];
  newRecord: any = {};
  years: any = [];
  constructor(private dataService: DataService, public snackBar: MatSnackBar) { }

  ngOnInit() {
    this.dataService.getDetails();
    this.user = this.dataService.user;
    this.getData();
  }
  ngAfterContentChecked() {
    this.getHeightWidth();
  }
  getHeightWidth() {
    let height = ($('.nav-list')[0].offsetTop - $('.students-view .card')[0].offsetTop),
      width = $('.sidenav-body')[0].offsetWidth;
    this.data.width = width - 45;
    this.data.height = height - 82;
    this.data.showFilter = true;
    this.data.showFooter = false;
    this.data.updatable = true;
    this.data.deletable = true;
  }
  getData() {
    let url = 'customers';
    this.data.url = url;
    this.data.deleteUrl = 'customers/';
    this.data.deleteMethod = true;
    this.data.editUrl = 'customers';
    this.fields = [
      {
        field: 'id',
        label: 'Id',
        element: 'input',
        hide: true,
        editable: false,
        update: true
      },
      {
        field: 'filePath',
        label: 'Documents',
        element: 'fileUpload',
        hide: false,
        editable: true,
        update: true
      },
      {
        field: 'userName',
        label: 'User Name',
        element: 'input',
        remove: true,
        updateField: true,
        hide: false,
        editable: false,
        update: true
      },
      {
        field: 'customerName',
        label: 'Customer Name',
        element: 'input',
        hide: false,
        editable: true,
        update: true
      },
      {
        field: 'gender',
        label: 'Gender',
        element: 'dropdown',
        hide: false,
        editable: true,
        values: [{
          id: 'male'
        }, {
          id: 'female'
        }],
        update: true
      },
      {
        field: 'dateOfBirth',
        label: 'Date Of Birth',
        element: 'date',
        hide: false,
        editable: true,
        update: true
      },
      {
        field: 'district',
        label: 'District',
        element: 'input',
        hide: false,
        editable: true,
        update: true
      },
      {
        field: 'city',
        label: 'City',
        element: 'input',
        hide: false,
        editable: true,
        update: true
      },
      {
        field: 'state',
        label: 'State',
        element: 'input',
        hide: false,
        editable: true,
        update: true
      },
      {
        field: 'noOfSheeps',
        label: 'Number Of Sheeps',
        element: 'number',
        hide: false,
        editable: true,
        update: true
      },
      {
        field: 'subsideryAmount',
        label: 'Subsidery Amount',
        element: 'input',
        hide: false,
        editable: true,
        update: true
      },
      {
        field: 'paidAmount',
        label: 'Paid Amount',
        element: 'input',
        hide: false,
        editable: true,
        update: true
      },
      {
        field: 'actions',
        label: 'Actions',
        element: '',
        hide: false,
        editable: true
      }
    ];
    this.data.data = [];
    this.dataService.getRequest(url).subscribe(results => {
      if (!results.length) {
        this.data.data = [];
      } else
        this.data.data = results;
      this.newRecord = {};
      this.callGrid = true;
    });
  }


  addNew(details) {
    let temp = {
      "city": details.city,
      "customerName":  details.customerName,
      "dateOfBirth":  this.dataService.convertDate(details.dateOfBirth, true),
      "district":  details.district,
      "filePath":  details.filePath,
      "gender":  details.gender,
      "noOfSheeps":  details.noOfSheeps,
      "paidAmount":  details.paidAmount,
      "state":  details.state,
      "subsideryAmount":  details.subsideryAmount,
      "userName":  details.userName,
    }
    let url = "customers";
    this.data.data = [];
    this.dataService.getData(url, temp).subscribe(results => {
      if (!results || results.code === -1) {
        this.snackBar.open('Error Adding Staff Details', 'Ok', {
          duration: 5000,
        });
        return;
      }
      this.dataService.getRequest(url).subscribe(results => {
        if (!results.length) {
          this.data.data = [];
          return;
        }
        this.flip();
        this.newRecord = {};
        this.data.data = results;
        this.snackBar.open('Staff Deatils Added Succesfully', 'Ok', {
          duration: 5000,
        });
      });
    });
  }

  onDelete(flag) {
    if (!flag.flag) {
      this.snackBar.open('Phase Details ' + (flag.text || flag) + ' Succesfully', 'Ok', {
        duration: 5000,
      });
    }
    this.callGrid = false;
    this.getData();
  }
  flip() {
    $('.card').toggleClass('flipped');
    this.frontVisible = !this.frontVisible;
  }
}




