import { Component, OnInit } from '@angular/core';
import { DataService } from './../../../services/data.service';
declare var jquery: any;
declare var $: any;
import { MatSnackBar } from '@angular/material';

@Component({
  selector: 'sheep-management',
  templateUrl: './sheep-management.component.html',
  styleUrls: ['./sheep-management.component.scss']
})
export class SheepManagementComponent implements OnInit {
  frontVisible: boolean = true;
  user: any;
  data: any = {};
  callGrid: boolean;
  fields: any = [];
  newRecord: any = {};
  years: any = [];
  phase: any = [];
  unit: any = [];
  owner: any = [];
  cattleType: any = [];
  categories: any = [];
  searchValue: any = 'All';
  inputValue: any = '';
  highValue: any = '';
  viewSearch: boolean = false;
  constructor(private dataService: DataService, public snackBar: MatSnackBar) { }

  ngOnInit() {
    this.dataService.getDetails();
    this.user = this.dataService.user;
    this.categories = ['All', 'Owner', 'Temperature', 'Weight', 'Date'];
    this.setValues();
  }
  setValues(){
    this.dataService.getRequest('customers').subscribe(results => {
      for (var i = 0; i < results.length; i++) {
        this.owner.push({ id: results[0].id , value:results[0].customerName });
      }
      this.dataService.getRequest('phase').subscribe(results => {
        for (let i = 0; i < results.length; i++) {
          this.phase.push({ id: results[0].id, value:results[0].phase });
        }
        this.dataService.getRequest('unit').subscribe(results => {
          for (let i = 0; i < results.length; i++) {
            this.unit.push({ id: results[0].id, value:results[0].unit });
          }
          this.dataService.getRequest('cattleType').subscribe(results => {
            for (let i = 0; i < results.length; i++) {
              this.cattleType.push({ id: results[0].id, value: results[0].cattleType });
            }
            this.fields = [
              {
                field: 'id',
                label: 'Id',
                element: 'input',
                hide: true,
                editable: false,
                update: true
              },
              {
                field: 'rfid',
                label: 'RFId',
                element: 'input',
                hide: true,
                remove: true,
                updateField: true,
                editable: false,
                update: true
              },
              {
                field: 'cattleType',
                label: 'Cattle Type',
                element: 'dropdown',
                hide: false,
                editable: false,
                update: true,
                value: true,
                values: this.cattleType
              },
              {
                field: 'age',
                label: 'Age',
                element: 'input',
                hide: false,
                editable: false,
                update: true
              },
              {
                field: 'contactNumber',
                label: 'contactNumber',
                element: 'input',
                hide: false,
                editable: false,
                update: true
              },
              {
                field: 'numberOfSheeps',
                label: 'Number Of Sheeps',
                element: 'number',
                hide: false,
                editable: false,
                update: true
              },
              {
                field: 'ownerName',
                label: 'Owner Id',
                element: 'dropdown',
                string: true,
                hide: false,
                editable: false,
                update: true,
                multiple: true,
                value: true,
                values: this.owner
              },
              {
                field: 'startDate',
                label: 'Start Date',
                element: 'date',
                hide: false,
                editable: false,
                update: true
              },
              {
                field: 'temperature',
                label: 'Temperature',
                element: 'input',
                hide: false,
                editable: false,
                update: true
              },
              {
                field: 'unitNumber',
                label: 'Unit Number',
                element: 'dropdown',
                hide: false,
                editable: false,
                value: true,
                update: true,
                values: this.unit
              },
              {
                field: 'weight',
                label: 'Weight',
                element: 'input',
                hide: false,
                editable: false,
                update: true
              },
              {
                field: 'actions',
                label: 'Actions',
                element: '',
                hide: false,
                editable: true
              }
            ];

            this.getData();
          });
        });
      });
    });
  }
  ngAfterContentChecked() {
    this.getHeightWidth();
  }
  categoryChange() {
    this.viewSearch = true;
    this.inputValue = '';
    this.highValue = '';
  }
  search() {
    this.callGrid = false;
    let urlTemp = '',
      value = this.searchValue;
    if (value === 'All') {
      urlTemp = 'sheep';
      this.data.data = [];
      this.dataService.getRequest(urlTemp).subscribe(results => {
        if (!results.length) {
          this.data.data = [];
        } else
          this.data.data = results;
        this.newRecord = {};
        this.callGrid = true;
      });
    }
    if (value === 'Date') {
      urlTemp = 'sheep/getBydate';
      let temp = {
        "endDate": this.dataService.convertDate(this.inputValue, true),
        "startDate": this.dataService.convertDate(this.highValue, true)
      }
      this.data.data = [];
      this.dataService.getData(urlTemp, temp).subscribe(results => {
        if (!results.length) {
          this.data.data = [];
        } else
          this.data.data = results;
        this.newRecord = {};
        this.callGrid = true;
      });
    }
    else {
      if (value === 'Temperature')
        urlTemp = 'getsheeps/' + this.inputValue;
      else if (value === 'Weight')
        urlTemp = 'sheepsby/' + this.inputValue;
      else
        urlTemp = 'sheeps/' + this.inputValue;
      this.data.data = [];
      this.dataService.getRequest(urlTemp).subscribe(results => {
        if (!results.length) {
          this.data.data = [];
        } else
          this.data.data = results;
        this.newRecord = {};
        this.callGrid = true;
      });
    }
  }
  getHeightWidth() {
    let height = ($('.nav-list')[0].offsetTop - $('.students-view .card')[0].offsetTop),
      width = $('.sidenav-body')[0].offsetWidth;
    this.data.width = width - 45;
    this.data.height = height - 82;
    this.data.showFilter = true;
    this.data.showFooter = false;
    this.data.updatable = true;
    this.data.deletable = true;
  }
  getData() {
    let url = 'sheep';
    this.data.url = url;
    this.data.deleteUrl = 'sheep/';
    this.data.deleteMethod = true;
    this.data.editUrl = 'sheep';
    this.data.data = [];
    this.dataService.getRequest(url).subscribe(results => {
      if (!results.length) {
        this.data.data = [];
      } else
        this.data.data = results;
      this.newRecord = {};
      this.callGrid = true;
    });
  }


  addNew(details) {
    let temp = {
      "age": details.age,
      "cattleType": details.cattleType,
      "contactNumber": details.contactNumber,
      "numberOfSheeps": details.numberOfSheeps,
      "ownerName": details.ownerName.join(','),
      "rfid": details.rfid,
      "startDate": this.dataService.convertDate(details.startDate, true),
      "temperature": details.temperature,
      "unitNumber": details.unitNumber,
      "weight": details.weight,
    }
    let url = "sheep";
    this.data.data = [];
    this.dataService.getData(url, temp).subscribe(results => {
      if (!results || results.code === -1) {
        this.snackBar.open('Error Adding Sheep Details', 'Ok', {
          duration: 5000,
        });
        return;
      }
      this.dataService.getRequest(url).subscribe(results => {
        if (!results.length) {
          this.data.data = [];
          return;
        }
        this.flip();
        this.newRecord = {};
        this.data.data = results;
        this.snackBar.open('Sheep Deatils Added Succesfully', 'Ok', {
          duration: 5000,
        });
      });
    });
  }

  onDelete(flag) {
    if (!flag.flag) {
      this.snackBar.open('Sheep Details ' + (flag.text || flag) + ' Succesfully', 'Ok', {
        duration: 5000,
      });
    }
    this.callGrid = false;
    this.getData();
  }
  flip() {
    this.setValues();
    $('.card').toggleClass('flipped');
    this.frontVisible = !this.frontVisible;
  }
}




