import { Component, OnInit } from '@angular/core';
import { DataService } from './../../../services/data.service';
declare var jquery: any;
declare var $: any;
import { MatSnackBar } from '@angular/material';

@Component({
  selector: 'phase-management',
  templateUrl: './phase-management.component.html',
  styleUrls: ['./phase-management.component.scss']
})
export class PhaseManagementComponent implements OnInit {
  frontVisible: boolean = true;
  user: any;
  data: any = {};
  callGrid: boolean;
  fields: any = [];
  newRecord: any = {};
  years: any = [];
  constructor(private dataService: DataService, public snackBar: MatSnackBar) { }

  ngOnInit() {
    this.dataService.getDetails();
    this.user = this.dataService.user;
    this.getData();
  }
  ngAfterContentChecked() {
    this.getHeightWidth();
  }
  getHeightWidth() {
    let height = ($('.nav-list')[0].offsetTop - $('.students-view .card')[0].offsetTop),
      width = $('.sidenav-body')[0].offsetWidth;
    this.data.width = width - 45;
    this.data.height = height - 82;
    this.data.showFilter = true;
    this.data.showFooter = false;
    this.data.updatable = true;
    this.data.deletable = true;
  }
  getData() {
    let url = 'phase';
    this.data.url = url;
    this.data.deleteUrl = 'phase/';
    this.data.deleteMethod = true;
    this.data.editUrl = 'phase';
    this.data.objTemp = { "businessId": this.dataService.user.businessId };
    this.fields = [
      {
        field: 'id',
        label: 'Phase Id',
        element: 'input',
        remove: true,
        updateField: true,
        hide: true,
        editable: false,
        update: true
      },
      {
        field: 'noOfUnits',
        label: 'Number Of Units',
        element: 'number',
        hide: false,
        editable: true,
        update: true
      },
      {
        field: 'phase',
        label: 'Phase',
        element: 'input',
        hide: false,
        editable: true,
        update: true
      },
      {
        field: 'actions',
        label: 'Actions',
        element: '',
        hide: false,
        editable: true
      }
    ];
    this.data.data = [];
    this.dataService.getRequest(url).subscribe(results => {
      if (!results.length) {
        this.data.data = [];
      } else
        this.data.data = results;
      this.newRecord = {};
      this.callGrid = true;
    });
  }


  addNewStudent(student) {
    let temp = {
      "noOfUnits": student.noOfUnits,
      "phase": student.phase
    }
    let url = "phase";
    this.data.data = [];
    this.dataService.getData(url, temp).subscribe(results => {
      if (!results || results.code === -1) {
        this.snackBar.open('Error Adding Phase Details', 'Ok', {
          duration: 5000,
        });
        return;
      }
      this.dataService.getRequest(url).subscribe(results => {
        if (!results.length) {
          this.data.data = [];
          return;
        }
        this.flip();
        this.newRecord = {};
        this.data.data = results;
        this.snackBar.open('Phase Deatils Added Succesfully', 'Ok', {
          duration: 5000,
        });
      });
    });
  }

  onDelete(flag) {
    if (!flag.flag) {
      this.snackBar.open('Phase Details ' + (flag.text || flag) + ' Succesfully', 'Ok', {
        duration: 5000,
      });
    }
    this.callGrid = false;
    this.getData();
  }
  flip() {
    $('.card').toggleClass('flipped');
    this.frontVisible = !this.frontVisible;
  }
}



