import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { HttpModule } from '@angular/http';
import { HttpClientModule } from '@angular/common/http';
import { FormsModule } from '@angular/forms';

import { AppComponent } from './app.component';
import { MaterialModule } from './material.module';
import { ApplicationsListComponent } from './components/applications-list/applications-list.component';
import { DataGridComponent } from './components/data-grid/data-grid.component';
import { DataService } from './services/data.service';
import { TodoDataService } from './todo-data.service';
import { PopupComponent } from './components/popup/popup.component';
import { ChartComponent } from './components/chart/chart.component';
import { DashboardComponent } from './modules/admin/dashboard/dashboard.component';

// Import angular2-fusioncharts
import { FusionChartsModule } from 'angular2-fusioncharts';

// Import FusionCharts library
import * as FusionCharts from 'fusioncharts';
// Import FusionCharts Charts module
import * as Charts from 'fusioncharts/fusioncharts.charts';
import { TodoListComponent } from './components/todo-list/todo-list.component';
import { FormComponent } from './components/form/form.component';
import { ModalDialogComponent } from './components/modal-dialog/modal-dialog.component';
//import {CalendarComponent} from "ap-angular2-fullcalendar";
import {CalendarComponent} from "angular2-fullcalendar/src/calendar/calendar";
import { FullCalendarModule } from 'ng-fullcalendar';
import { StarRatingComponent } from './components/star-rating/star-rating.component';
import { LoginComponent } from './components/login/login.component';
import { MainComponent } from './components/main/main.component';
import { RoutingModule } from './/routing.module';
import { PhaseManagementComponent } from './modules/admin/phase-management/phase-management.component';
import { UnitsManagementComponent } from './modules/admin/units-management/units-management.component';
import { SheepManagementComponent } from './modules/admin/sheep-management/sheep-management.component';
import { StaffManagementComponent } from './modules/admin/staff-management/staff-management.component';
import { CustomersManagementComponent } from './modules/admin/customers-management/customers-management.component';
import { ExpensesManagementComponent } from './modules/admin/expenses-management/expenses-management.component';
import { SecurityManagementComponent } from './modules/admin/security-management/security-management.component';
import { CattleManagementComponent } from './modules/admin/cattle-management/cattle-management.component';

@NgModule({
	declarations: [
		AppComponent,
		ApplicationsListComponent,
		DataGridComponent,
		PopupComponent,
		DashboardComponent,
		ChartComponent,
		TodoListComponent,
		FormComponent,
		ModalDialogComponent,
		StarRatingComponent,
		LoginComponent,
		MainComponent,
		PhaseManagementComponent,
		UnitsManagementComponent,
		SheepManagementComponent,
		StaffManagementComponent,
		CustomersManagementComponent,
		ExpensesManagementComponent,
		SecurityManagementComponent,
		CattleManagementComponent
	],
	entryComponents: [PopupComponent],
	imports: [
		BrowserModule,
		MaterialModule,
		BrowserAnimationsModule,
		HttpModule,
		HttpClientModule,
		FormsModule,
		FullCalendarModule,
		FusionChartsModule.forRoot(FusionCharts, Charts),
		RoutingModule,
	],
	providers: [DataService, TodoDataService],
	bootstrap: [AppComponent]
})
export class AppModule { }



