import { Component, OnInit } from '@angular/core';
import { DataService } from '../../../services/data.service';
declare var jquery: any;
declare var $: any;
import { MatSort, MatSortable, MatTableDataSource, MatPaginator, MatDialog } from '@angular/material';
import { PopupComponent } from '../../../components/popup/popup.component';
import { MatSnackBar } from '@angular/material';


@Component({
  selector: 'expenses-management',
  templateUrl: './expenses-management.component.html',
  styleUrls: ['./expenses-management.component.scss']
})
export class ExpensesManagementComponent implements OnInit {
  frontVisible: boolean = true;
  user: any;
  data: any = {};
  callGrid: boolean;
  fields: any = [];
  searchFields: any = [];
  newRecord: any = {};
  years: any = [];
  search: any;
  feedbackConfig: any;
  department: any;
  selected: any;
  rowHeight: any;
  callGraph: boolean = false;
  addDetail: boolean = false;
  url: any;
  constructor(private dataService: DataService, public dialog: MatDialog, public snackBar: MatSnackBar) { }

  dataChanged(event) {
    let department = event.value;
    this.selected = this.dataService.getObjectBy(this.department, {value: department}).name;
    let urlTemp = '';
    this.data = [];
    // this.data.dynamicColumns = true;
    this.addDetail = false;
    if (department === 1) {
      urlTemp = 'stockExpenses';
      this.fields = [];
      this.fields = [
        {
          field: 'id',
          label: 'Id',
          element: 'input',
          hide: true,
          editable: false,
          update: true
        },
        {
          field: 'invoiceNumber',
          label: 'Invoice Number',
          element: 'input',
          updateField: true,
          remove: true,
          hide: false,
          editable: false,
          update: true
        },
        {
          field: 'invoiceDate',
          label: 'Invoice Date',
          element: 'date',
          hide: false,
          editable: false,
          update: true
        },
        {
          field: 'merchantName',
          label: 'Merchant Name',
          element: 'input',
          hide: false,
          editable: false,
          update: true
        },
        {
          field: 'numberOfItems',
          label: 'Number Of Items',
          element: 'number',
          hide: false,
          editable: false,
          update: true
        },
        {
          field: 'totalAmount',
          label: 'Total Amount',
          element: 'number',
          hide: false,
          editable: false,
          update: true
        },
        {
          field: 'typeOfStock',
          label: 'Type Of Stock',
          element: 'input',
          hide: false,
          editable: false,
          update: true
        },
        {
          field: 'uploadFile',
          label: 'Upload File',
          element: 'input',
          hide: false,
          editable: false,
          update: true
        },
        {
          field: 'actions',
          label: 'Actions',
          element: '',
          hide: false,
          editable: true
        }
      ];
    } else if (department === 2) {
      urlTemp = 'unitExpenses';
      this.fields = [];
      this.fields = [
        {
          field: 'id',
          label: 'Id',
          element: 'input',
          hide: true,
          editable: false,
          update: true
        },
        {
          field: 'invoiceNumber',
          label: 'Invoice Number',
          element: 'input',
          updateField: true,
          remove: true,
          hide: false,
          editable: false,
          update: true
        },
        {
          field: 'invoiceDate',
          label: 'Invoice Date',
          element: 'date',
          hide: false,
          editable: false,
          update: true
        },
        {
          field: 'merchantName',
          label: 'Merchant Name',
          element: 'input',
          hide: false,
          editable: false,
          update: true
        },
        {
          field: 'quantity',
          label: 'Quantity',
          element: 'number',
          hide: false,
          editable: false,
          update: true
        },
        {
          field: 'totalAmount',
          label: 'Total Amount',
          element: 'number',
          hide: false,
          editable: false,
          update: true
        },
        {
          field: 'typeOfAssets',
          label: 'Type Of Assets',
          element: 'input',
          hide: false,
          editable: false,
          update: true
        },
        {
          field: 'uploadFile',
          label: 'Upload File',
          element: 'input',
          hide: false,
          editable: false,
          update: true
        },
        {
          field: 'actions',
          label: 'Actions',
          element: '',
          hide: false,
          editable: true
        }
      ];
    }
    else if (department === 3) {
      urlTemp = 'wages';
      this.fields = [];
      this.fields = [
        {
          field: 'id',
          label: 'Id',
          element: 'input',
          hide: true,
          editable: false,
          update: true
        },
        {
          field: 'employeeIdentity',
          label: 'Employee Identity',
          element: 'input',
          updateField: true,
          remove: true,
          hide: false,
          editable: false,
          update: true
        },
        {
          field: 'employeeName',
          label: 'Employee Name',
          element: 'input',
          hide: false,
          editable: false,
          update: true
        },
        {
          field: 'paymentDate',
          label: 'Payment Date',
          element: 'date',
          hide: false,
          editable: false,
          update: true
        },
        {
          field: 'designation',
          label: 'Designation',
          element: 'input',
          hide: false,
          editable: false,
          update: true
        },
        {
          field: 'salaryPaid',
          label: 'Salary Paid',
          element: 'number',
          hide: false,
          editable: false,
          update: true
        },
        {
          field: 'actions',
          label: 'Actions',
          element: '',
          hide: false,
          editable: true
        }
      ];

    }
    this.url = urlTemp;
    this.getData(urlTemp);
  }
  ngOnInit() {
    this.user = this.dataService.user;
    this.department = [
      { name: 'Stock', value: 1 },
      { name: 'Unit', value: 2 },
      { name: 'Wages', value: 3 }
    ];
    this.getTotalExpenses();
  }
  getTotalExpenses(){
    this.dataService.getRequest('totalExpenses').subscribe(results => {
      if (results) {
        results = results;
        this.feedbackConfig = {};
        let height = ($('.nav-list')[0].offsetTop - $('.sidenav-body')[0].offsetTop) / 2;
        this.rowHeight = height + 'px';
        this.feedbackConfig = {
          type: 'column2d',
          height: height,
          width: 700,
          data: {
            chart: {
              caption: 'Expenses',
              //subCaption: 'Class Wise Results',
              //numberPrefix: '%',
              //numberSuffix: '%',
              //'theme': 'ocean'
              "theme": "fint",
              usePlotGradientColor: 0
            },
            data: [
              {
                label: 'Total',
                value: results.expensesTotal !== null ? results.expensesTotal : 0
              },
              {
                label: 'Stock',
                value: results.totalStockExpenses !== null ? results.totalStockExpenses : 0,
                color: '#0000FF'
              },
              {
                label: 'Unit',
                value: results.totalUnitExpenses !== null ? results.totalUnitExpenses : 0,
                color : '#FFA500'
              },
              {
                label: 'Wages',
                value: results.totalWages !== null ? results.totalWages : 0,
                color : '#008000'
              }
            ]
          }
        };
      }
      this.callGraph = true;
    });
  }
  addNew(details) {
    let url = this.url;
    this.callGrid = false;
    let temp = {},
    urlTemp = '';
    if (url === 'stockExpenses') {
      temp = {
        "invoiceDate": this.dataService.convertDate(details.invoiceDate, true),
        "invoiceNumber": details.invoiceNumber,
        "merchantName": details.merchantName,
        "numberOfItems": details.numberOfItems,
        "totalAmount": details.totalAmount,
        "typeOfStock": details.typeOfStock,
        "uploadFile": details.uploadFile,
      }
      urlTemp = 'stockExpenses';
    }
    else if (url === 'unitExpenses') {
      temp = {
        "invoiceDate": this.dataService.convertDate(details.invoiceDate, true),
        "invoiceNumber": details.invoiceNumber,
        "merchantName": details.merchantName,
        "quantity": details.quantity,
        "totalAmount": details.totalAmount,
        "typeOfAssets": details.typeOfAssets,
        "uploadFile": details.uploadFile,
      }
      urlTemp = 'unitExpenses';
    }
    else if (url === 'wages') {
      temp = {
        "designation": details.designation,
        "employeeIdentity": details.employeeIdentity,
        "employeeName": details.employeeName,
        "paymentDate": this.dataService.convertDate(details.paymentDate, true),
        "salaryPaid": details.salaryPaid,
      }
      urlTemp = url;
    }
    this.data.data = [];
    this.dataService.getData(url, temp).subscribe(results => {
      if (!results || results.code === -1) {
        this.snackBar.open('Error Adding Expense Details', 'Ok', {
          duration: 5000,
        });
        return;
      }
      this.dataService.getRequest(url).subscribe(results => {
        if (!results.length) {
          this.data.data = [];
          return;
        }
        this.addDetail = false;
        this.newRecord = {};
        this.data.data = results;
        this.callGrid = true;
        this.snackBar.open('Expense Deatils Added Succesfully', 'Ok', {
          duration: 5000,
        });
      });
    });
  }

  onDelete(flag) {
    if (!flag.flag) {
      this.snackBar.open('Expense Details ' + (flag.text || flag) + ' Succesfully', 'Ok', {
        duration: 5000,
      });
    }
    this.callGrid = false;
    this.getData(this.url);
  }

  processChartData(data) {
    for (var i = 0; i < data.length; i++) {
      let record = data[i];
      if (record.value < 3) {
        record.color = '#ff0000';
        record.isSliced = "1";
      }
      else if (record.value >= 3 && record.value < 4)
        record.color = '#0000FF';
      else if (record.value >= 4 && record.value < 5)
        record.color = '#FFA500';
      else
        record.color = '#008000';
    }
    return data;
  }

  ngAfterContentChecked() {
    this.getHeightWidth();
  }
  getHeightWidth() {
    let height = ($('.nav-list')[0].offsetTop - $('.students-view .card')[0].offsetTop),
      width = $('.sidenav-body')[0].offsetWidth;
    this.data.width = width - 45;
    this.data.height = height - 82;
    this.data.showFilter = true;
    this.data.showFooter = false;
    this.data.updatable = false;
    this.data.deletable = false;
    this.data.details = false;
  }

  getData(urlTemp) {
    let url = urlTemp;
    this.data.url = url;
    this.data.deleteUrl = this.data.url + '/';
    this.data.deleteMethod = true;
    this.data.editUrl = this.data.url;
    this.dataService.getRequest(url).subscribe(results => {
      if (!results) {
        this.data.data = [];
      } else
        if (results)
          this.data.data = results;
        else this.data.data = [];
      // this.data.dynamicColumns = true;
      this.newRecord = {};
      this.callGrid = true;
    });
  }

  addNewStudent(student) {
    this.data.data.push(this.newRecord);
    this.flip();
    this.dataService.getDetails();
  }
  flip() {
    $('.card').toggleClass('flipped');
    this.frontVisible = !this.frontVisible;
    this.getTotalExpenses();
  }

}



