import { Component, OnInit } from '@angular/core';
import { DataService } from './../../../services/data.service';
declare var jquery: any;
declare var $: any;

@Component({
  selector: 'admin-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.scss']
})
export class DashboardComponent implements OnInit {
  tile: any = {};
  constructor(private dataService: DataService) { }
  chartConfig: any = {};
  profitsConfig: any = {};
  lowerWeightDetails: any = {};
  lowerTempDetails: any = {};
  feedbackData: any = [];
  ngOnInit() {
    this.tile.color = 'white';
    //this.tile.rowHeight = ($('.sidenav-body')[0].height() / 3 );
  }
  processChartData(data, max?) {
    for (var i = 0; i < data.length; i++) {
      let record = data[i],
        value = max ? data[i].value * 20 : data[i].value;
      if (value < 40) {
        record.color = '#ff0000';
        record.isSliced = "1";
      }
      else if (value >= 40 && value < 50)
        record.color = '#0000FF';
      else if (value >= 50 && value < 80)
        record.color = '#FFA500';
      else
        record.color = '#008000';
    }
    return data;
  }
  ngAfterContentChecked() {

    let padding = 60,
      height = ($('.nav-list')[0].offsetTop - $('.sidenav-body')[0].offsetTop - 50) / 2,
      width = $('.mat-grid-tile')[0].offsetWidth;
    this.tile.rowHeight = height + 'px';
    this.profitsConfig = {
      height: height,
      width: $('.mat-grid-tile')[0].offsetWidth,
      type: 'column2d',
      data: {
        chart: {
          caption: 'Profits',
          subCaption: 'Month Wise Profits',
          //numberPrefix: '%',
          numberSuffix: '%',
          //"palette": "1",
          usePlotGradientColor: 0,
          valueFontColor: '#fff',
          placeValuesInside: 1
          //'theme': 'ocean',
        },
        data: this.processChartData([
          {
            label: 'January',
            value: '65',
            color: '#f00'
          },
          {
            label: 'February',
            value: '81'
          },
          {
            label: 'March',
            value: '70'
          },
          {
            label: 'April',
            value: '59'
          },
          {
            label: 'May',
            value: '83'
          },
          {
            label: 'June',
            value: '82'
          },
          {
            label: 'July',
            value: '82'
          }
        ])
      }
    };
    this.lowerTempDetails = {
      height: height,
      width: $('.mat-grid-tile')[1].offsetWidth,
      showFilter: false,
      showFooter: false,
      fields: [
        { field: 'SNo', label: 'S.Id' },
        { field: 'Name', label: 'Name' },
        { field: 'Temperature', label: 'Temperature' },
      ],
      data: [
        { "SNo": "1", "Name": "Sheep 1", "Temperature": "42" },
        { "SNo": "2", "Name": "Sheep 2", "Temperature": "45" },
        { "SNo": "3", "Name": "Sheep 3", "Temperature": "46" },
        { "SNo": "4", "Name": "Sheep 4", "Temperature": "49" },
        { "SNo": "5", "Name": "Sheep 5", "Temperature": "52" },
        { "SNo": "6", "Name": "Sheep 6", "Temperature": "55" },
        { "SNo": "7", "Name": "Sheep 7", "Temperature": "60" }
      ]
    };
    this.lowerWeightDetails = {
      height: height,
      width: $('.mat-grid-tile')[2].offsetWidth,
      showFilter: false,
      showFooter: false,
      fields: [
        { field: 'SNo', label: 'S.Id' },
        { field: 'Name', label: 'Name' },
        { field: 'Weight', label: 'Weight' },
      ],
      data: [
        { "SNo": "1", "Name": "Sheep 1", "Weight": "42" },
        { "SNo": "2", "Name": "Sheep 2", "Weight": "45" },
        { "SNo": "3", "Name": "Sheep 3", "Weight": "46" },
        { "SNo": "4", "Name": "Sheep 4", "Weight": "49" },
        { "SNo": "5", "Name": "Sheep 5", "Weight": "52" },
        { "SNo": "6", "Name": "Sheep 6", "Weight": "55" },
        { "SNo": "7", "Name": "Sheep 7", "Weight": "60" }
      ]
    };
    this.chartConfig.height = height;
    this.chartConfig.width = width;
  }

}



