import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import { Router } from '@angular/router';
import { MatSnackBar } from '@angular/material';

@Injectable()
export class DataService {
    public selectedApp: object = {};
    public user: any;
    departments: any;
    sections: any;
    courses: any;
    subjects: any;
    roles: any = {
        1: 'Admin',
        2: 'Professor',
        3: 'Library',
        4: 'Transport',
        5: 'Parent',
        6: 'Student',
        7: 'HOD',
        9: 'Hostel'
    };
    private serviceURL = 'https://randomapi.com/api/6de6abfedb24f889e0b5f675edc50deb?fmt=raw&sole';
    private commonUrl = 'http://13.127.175.211:8088/magnus-management/v1.0/';
    constructor(private http: HttpClient, public router: Router, public snackBar: MatSnackBar) {
    }
    public uploadFile(fileToUpload: File): Observable<any> {
        const _formData = new FormData();
        _formData.append('file', fileToUpload, fileToUpload.name);
        var a = this.http.post('http://13.127.175.211:8088/magnus-management/v1.0/file', _formData); //note: no HttpHeaders passed as 3d param to POST!
        //So no Content-Type constructed manually.
        //Angular 4.3/4.x does it automatically.
        return a;   
    }
    reset(details) {
        let url = "forgetPassword";
        this.putData(this.commonUrl + url, details).subscribe(results => {
            if (results.code === -1) {
                this.snackBar.open(results.message, 'Ok', {
                    duration: 5000,
                });
                return;
            }
            this.snackBar.open('Password Reset Successfull', 'Ok', {
                duration: 5000,
            });
            this.user = results.data;
            this.router.navigateByUrl('/magnus');
        });
    }
    signUp(url, details) {
        this.user = {
            selectedRole: 'Admin',
            selectedApp: this.selectedApp
        }
        this.getData(url, details).subscribe(results => {
            if (!results) {
                this.snackBar.open(results.message, 'Ok', {
                    duration: 5000,
                });
                return;
            }
            this.snackBar.open('Sign Up Successfull', 'Ok', {
                duration: 5000,
            });
            this.user.userName = details.userName;
            this.user.id = results.id;
            this.user.selectedRole = 'Admin';
            this.router.navigateByUrl('/magnus');
        });
    }
    login(details): Observable<any[]> {
        let url = '';
        this.user = {
            selectedRole: 'Admin',
            selectedApp: this.selectedApp
        }
        this.user.selectedRole = details.username;
        if (!details.password) {
            this.snackBar.open('Enter Password', 'Ok', {
                duration: 5000,
            });
        }
        else if (details.username && details.password) {
            url = "user/" + details.username + "/user/" + details.password;
            this.getRequest(url).subscribe(results => {
                if (!results) {
                    this.snackBar.open('Invalid Username or Password', 'Ok', {
                        duration: 5000,
                    });
                    return;
                }
                this.user.userName = results.userName;
                this.user.id = results.id;
                this.user.selectedRole = 'Admin';
                this.router.navigateByUrl('/magnus');
            });
        }
        return this.user;
    };
    getUser(app): Observable<any[]> {
        return this.http.get<any[]>(this.serviceURL);
    }
    deleteData(url): Observable<any> {
        return this.http.delete(this.commonUrl + url);
    }

    getData(url, data): Observable<any> {
        return this.http.post<any>(this.commonUrl + url, data);
        // return this.http.get<any[]>(url);
    }

    addDetail(url): Observable<any[]> {
        return this.http.get<any[]>(url);
    }

    getRequest(url): Observable<any> {
        return this.http.get<any>(this.commonUrl + url);
    }

    putData(url, data): Observable<any> {
        return this.http.put<any>(this.commonUrl + url, data);
    }

    getObjectBy(data, filter) {
        var _return: any;
        for (var i = 0; i < data.length; i++) {
            if (data.hasOwnProperty(i) && this._searchObject(data[i], filter)) {
                _return = data[i];
            }
        }
        return _return ? _return : undefined;
    }

    _searchObject(obj, filter) {
        var found = false;
        var key = Object.keys(filter)
        if (obj[key[0]] == filter[key[0]])
            found = true;
        else
            found = false;
        return found;
    }
    getDetails() {

    }
    getObjectsBy(data, filter) {
        var arrResult = [];
        for (var i = 0; i < data.length; i++) {
            if (data.hasOwnProperty(i) && this._searchObject(data[i], filter)) {
                arrResult.push(data[i]);
            }
        }
        return arrResult;
    }

    convertDate(date, out) {
        if (date) {
            if (out) {
                return date.valueOf();
            }
            else {
                let d = new Date(date);
                let month = String(d.getMonth() + 1);
                let day = String(d.getDate());
                const year = String(d.getFullYear());

                if (month.length < 2) month = '0' + month;
                if (day.length < 2) day = '0' + day;

                return `${day}/${month}/${year}`;
            }
        }
        else {
            return '';
        }
    }

}



