import { Component, OnInit } from '@angular/core';
import { DataService } from './../../../services/data.service';
declare var jquery: any;
declare var $: any;
import { MatSnackBar } from '@angular/material';

@Component({
  selector: 'staff-management',
  templateUrl: './staff-management.component.html',
  styleUrls: ['./staff-management.component.scss']
})
export class StaffManagementComponent implements OnInit {
  frontVisible: boolean = true;
  user: any;
  data: any = {};
  callGrid: boolean;
  fields: any = [];
  newRecord: any = {};
  years: any = [];
  constructor(private dataService: DataService, public snackBar: MatSnackBar) { }

  ngOnInit() {
    this.dataService.getDetails();
    this.user = this.dataService.user;
    this.getData();
  }
  ngAfterContentChecked() {
    this.getHeightWidth();
  }
  getHeightWidth() {
    let height = ($('.nav-list')[0].offsetTop - $('.students-view .card')[0].offsetTop),
      width = $('.sidenav-body')[0].offsetWidth;
    this.data.width = width - 45;
    this.data.height = height - 82;
    this.data.showFilter = true;
    this.data.showFooter = false;
    this.data.updatable = true;
    this.data.deletable = true;
  }
  getData() {
    let url = 'staffs';
    this.data.url = url;
    this.data.deleteUrl = 'staffs/';
    this.data.deleteMethod = true;
    this.data.editUrl = 'staffs';
    this.data.columns = ["id","firstName","lastName","gender","salary","designation","documents","userName"];
    this.data.objTemp = { "businessId": this.dataService.user.businessId };
    this.fields = [
      {
        field: 'id',
        label: 'Id',
        element: 'input',
        hide: true,
        editable: false,
        update: true
      },
      {
        field: 'documents',
        label: 'Documents',
        element: 'fileUpload',
        hide: false,
        editable: true,
        update: true
      },
      {
        field: 'userName',
        label: 'User Name',
        element: 'input',
        remove: true,
        updateField: true,
        hide: false,
        editable: false,
        update: true
      },
      {
        field: 'firstName',
        label: 'First Name',
        element: 'input',
        hide: false,
        editable: true,
        update: true
      },
      {
        field: 'lastName',
        label: 'Last Name',
        element: 'input',
        hide: false,
        editable: true,
        update: true
      },
      {
        field: 'gender',
        label: 'Gender',
        element: 'dropdown',
        hide: false,
        editable: true,
        values: [{
          id: 'male'
        }, {
          id: 'female'
        }],
        update:true
      },
      {
        field: 'designation',
        label: 'Designation',
        element: 'input',
        hide: false,
        editable: true,
        update: true
      },
      {
        field: 'salary',
        label: 'Salary',
        element: 'input',
        hide: false,
        editable: true,
        update: true
      },
      {
        field: 'password',
        label: 'Password',
        element: 'input',
        hide: false,
        editable: true,
        update: true
      },
      {
        field: 'confirmPassword',
        label: 'Confirm Password',
        element: 'input',
        hide: false,
        editable: true,
        update: true
      },
      {
        field: 'actions',
        label: 'Actions',
        element: '',
        hide: false,
        editable: true
      }
    ];
    this.data.data = [];
    this.dataService.getRequest(url).subscribe(results => {
      if (!results.length) {
        this.data.data = [];
      } else
        this.data.data = results;
      this.newRecord = {};
      this.callGrid = true;
    });
  }

  addNew(details) {
    let temp = {
      "confirmPassword": details.confirmPassword,
      "designation": details.designation,
      "documents": details.documents,
      "firstName": details.firstName,
      "gender": details.gender,
      "lastName": details.lastName,
      "password": details.password,
      "salary": details.salary,
      "userName": details.userName,
    }
    let url = "staffs";
    this.data.data = [];
    this.dataService.getData(url, temp).subscribe(results => {
      if (!results || results.code === -1) {
        this.snackBar.open('Error Adding Staff Details', 'Ok', {
          duration: 5000,
        });
        return;
      }
      this.dataService.getRequest(url).subscribe(results => {
        if (!results.length) {
          this.data.data = [];
          return;
        }
        this.flip();
        this.newRecord = {};
        this.data.data = results;
        this.snackBar.open('Staff Deatils Added Succesfully', 'Ok', {
          duration: 5000,
        });
      });
    });
  }

  onDelete(flag) {
    if (!flag.flag) {
      this.snackBar.open('Staff Details ' + (flag.text || flag) + ' Succesfully', 'Ok', {
        duration: 5000,
      });
    }
    this.callGrid = false;
    this.getData();
  }
  flip() {
    $('.card').toggleClass('flipped');
    this.frontVisible = !this.frontVisible;
  }
}




