import { Component, OnInit, HostListener } from '@angular/core';
import { OverlayContainer } from '@angular/cdk/overlay';
import { DataService } from './../../services/data.service';
declare var jquery: any;
declare var $: any;
import { Router } from '@angular/router';

@Component({
	selector: 'app-main',
	templateUrl: './main.component.html',
	styleUrls: ['./main.component.scss']
})
export class MainComponent implements OnInit {
	title = 'app';
	mode: string;
	open: boolean;
	selectedApp: Object;
	appList: any[];
	user: any;
	showMenu: any;
	showLogin: boolean = true;
	userName: any;
	password: any;
	constructor(public overlayContainer: OverlayContainer, public router: Router, private dataService: DataService) {
		this.configureSideNav();

	}
	@HostListener('window:resize', ['$event'])
	onResize(event) {
		this.configureSideNav()
	}
	ngOnInit() {
		this.user = this.dataService.user;
		if (!this.user) {
			this.router.navigateByUrl('');
		}
		else {
			setTimeout(function () { $('.list-wrapper').fadeOut() }, 5000);
			setTimeout(function () {
				$(".nav-list").mouseover(function () {
					$('.list-wrapper').fadeIn();
				});
				$('.nav-list').mouseleave(function () {
					setTimeout(function () { $('.list-wrapper').fadeOut() }, 1000);
				});
			}, 5000);

			if (this.user.selectedRole === 'Admin') {
				this.appList = [
					{ id: 1, name: 'Dashboards', icon: 'D' },
					{ id: 2, name: 'Phase Management', icon: 'S' },
					{ id: 3, name: 'Units Management', icon: 'P' },
					{ id: 4, name: 'Sheep Management', icon: 'E' },
					{ id: 5, name: 'Staff Management', icon: 'L' },
					{ id: 6, name: 'Customers', icon: 'A' },
					{ id: 7, name: 'Expenses', icon: 'R' },
					{ id: 8, name: 'Cattle Management', icon: 'R' },
					{ id: 9, name: 'Security Management', icon: 'F' }
				];
			}
		}
	}
	ngOnChanges() {
		this.user = this.dataService.user;
		setTimeout(function () { $('.list-wrapper').hide() }, 5000);
		$(".nav-list").mouseover(function () {
			console.log('oninit show');
			$('.list-wrapper').show();
		});
		$('.nav-list').mouseleave(function () {
			setTimeout(function () { console.log('init leave'); $('.list-wrapper').hide() }, 1000);
		});
	}

	changeTheme() {
		document.body.classList.toggle('light-theme');
		// this.overlayContainer.getContainerElement().classList.toggle('light-theme');
	}


	onAppSelect(item) {
		this.selectedApp = item;
		this.dataService.user.selectedApp = this.user.selectedApp = item;
	}
	onLogin() {
		let details = {
			username: this.userName,
			password: this.password
		};
		let data = this;
		this.dataService.login(details).subscribe(data => {
			this.loginSucess(data);
		})
	};

	logOut() {
		this.dataService.getRequest('logout/'+this.dataService.user.userName).subscribe(results => {
			if (!results || results.code === -1) {
				return;
			}
			this.user = {};
			this.router.navigateByUrl('');
		});
		this.router.navigateByUrl('');
	};

	loginSucess(data) {
		let appList: any = {};
		this.user = data.dataService.user;
	}
	configureSideNav() {

		if (window.innerWidth < 950) {
			this.mode = "over"
			this.open = false
		} else {
			this.mode = 'side'
			this.open = true
		}
	}
}



